//---------------------------------------------------------//
// Tabs
//---------------------------------------------------------// 

.tab-content {
	width: 100%;
  > .tab-pane {
    display: none;
  }
  > .active {
    display: block;
  }
}



// Button Tabs

.btn-tabs {
	margin-bottom: rem(-10);
	li {
		margin-right: rem(10);
		display: inline-block;
		margin-bottom: rem(10);
		&:last-child {
			margin-right: 0;
		}
	}
	.btn {
		opacity: 0.7;
		color: $white;
		background-color: $primary;
		-webkit-transition: all $easing-speed $easing;
		transition: all $easing-speed $easing;
	}
	.btn:hover, .btn.active {
		opacity: 1;
		background-color: $primary;
		border-color: $primary;
	}
}


.all-tabs {
	margin-bottom: rem(30);
}
.all-tab-offset {
	margin-top: rem(-65);
	margin-bottom: rem(30);
}