//---------------------------------------------------------//
// Tables
//---------------------------------------------------------// 


.table {
    &.data-table {
        white-space: nowrap;
    }
    h1, h2, h3, h4, h5, h6, p, ul:not(.list-inline) {
        &:last-child {
            margin-bottom: 0;
        }
    }
    thead {
        th {
            border-bottom-width: 1px;
            padding-top: 0;
            font-size: 11px;
            font-weight: bold;
            color: $gray;
            padding-bottom: 20px;
            button {
                font-weight: bold;
                color: $gray;
                white-space: nowrap;
            }
        }
    }
    th, td {
        border-top: none;
        border-bottom: 1px solid $table-border-color;
        padding-left: 0;
        padding-right: 15px;
        padding-top: 15px;
        padding-bottom: 15px;
        vertical-align: middle;
        position: relative;
        &:not(.data-checkbox) {
            min-width: 80px;
        }
        // &:last-child {
        //     text-align: right;
        //     padding-right: 0;
        // }
    }
    tr {
        &:hover {
            .data-muted {
                opacity: 0.25;
            }
            .data-tools {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    .data-checkbox {
        width: 22px;
        min-width: 22px;
        padding-right: 0;
    }
    .data-thumbnail {
        border: 1px solid $border-color;
        background-color: $gray-white;
        img {
            max-width: 29px;
        }
    }
    .data-muted {
        color: $gray;
        -webkit-transition: opacity $easing-speed $easing;
        transition: opacity $easing-speed $easing;
    }
    .data-tools {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        white-space: nowrap;
        z-index: 2;
        opacity: 0;
        margin-top: 1px;
        visibility: hidden;
        -webkit-transition: all $easing-speed $easing;
        transition: all $easing-speed $easing;
    }
    .tool-btn {
        border: 1px solid $gray-lighter;
        width: 24px;
        height: 24px;
        line-height: 24px;
        text-align: center;
        border-radius: $border-radius;
        display: inline-block;
        background-color: $white;
        &:hover, &:focus {
            color: $primary;
        }
    }
}




// Inline Table

.inline-table, .block-table {
    display: table;
    margin-bottom: $globalMarginBottom;
    &:last-child {
        margin-bottom: 0;
    }
    .td {
        display: table-cell;
        vertical-align: middle;
        padding-right: 10px;
        &.v-top {
            vertical-align: top;
        }
        &:last-child {
            padding-right: 0;
        }
    }
    h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6, p {
        &:last-child {
            margin-bottom: 0;
        }
    }
    @include media-breakpoint-down(sm) {
        &.table-block-mobile {
            text-align: center;
            display: block;
            .td {
                display: block;
                padding-right: 0;
            }
        }
    }
    @include media-breakpoint-up(sm) {
        .td {
            padding-right: 15px;
        }
    }
    @include media-breakpoint-up(md) {
        .td {
            padding-right: 20px;
        }
    }
}


// Block Table

.block-table {
    display: table;
    width: 100%;
    .td {
        &:last-child {
            > * {
                margin-left: auto;
            }
            text-align: right;
        }
    }
}