//---------------------------------------------------------//
// Navigation
//---------------------------------------------------------// 
// - All Menus
//---------------------------------------------------------//


.nav {
	list-style: none;
	padding-left: 0;
}




// Sidenav

.sidenav, .sidenav ul {
	@extend .nav;
}

.sidenav {
	@extend .text-md;
	font-weight: bold;
	margin-top: -8px;
	> li > a {
		background: url('#{$icon-url}/icon-building-front.svg') 20px 50% no-repeat;
		background-size: 10px 13px;
		padding-top: 7px;
		padding-bottom: 7px;
		margin-left: -20px;
		margin-right: -20px;
		padding-left: 40px;
		padding-right: 20px;
		border-top: 1px solid transparent;
		border-bottom: 1px solid transparent;
	}
	a {
		display: block;
		color: $body-color;
	}
	li.active a, a:hover {
		color: $primary;
		text-decoration: none;
	}
	li > a.active {
		border-top: 1px solid $border-color;
		border-bottom: 1px solid $border-color;
		margin-left: -20px;
		margin-right: -20px;
		padding-left: 40px;
		padding-right: 20px;
	}
	ul {
		font-size: 11px;
		background-color: $body-bg;
		margin-left: -20px;
		margin-right: -20px;
		padding-left: 40px;
		
		border-bottom: 1px solid $border-color;
		li {
			&:first-child {
				padding-top: 5px;
			}
			&:last-child {
				padding-bottom: 7px;
			}
		}
		a {
			padding-top: 7px;
			padding-bottom: 7px;
		}
	}
}





// Nav Tabs

.nav-tabs {
	border-bottom: 1px solid $border-color;
	display: flex;
	li {
		margin-bottom: -1px;
		&:last-child {
			a {
				border-right-color: transparent;
			}
		}
	}
	a {
		color: $gray;
		@extend .btn;
		padding-left: 15px;
		padding-right: 15px;
		border-radius: 0;
		box-shadow: none !important;
		display: block;
		border-right: 1px solid $border-color;
		&:hover, &.active {
			background-color: $white;
			text-decoration: none;
		}
		&:not(.active):hover {
			border-bottom: 1px solid $border-color;
		}
		&.active {
			position: relative;
			z-index: 2;
			color: $body-color;
			border-right-color: $border-color !important;
		}
	}
}