//---------------------------------------------------------//
// TYPOGRAPHY
//---------------------------------------------------------// 
// - All types of typography
// - Font-face
// - Headings
// - Paragraphs
//---------------------------------------------------------//


@import url('https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500,500i,700,700i');

@font-face {
    font-family: 'AvenirLTStd';
    src: url('#{$font-url}/AvenirLTStd-Black.woff2') format('woff2'),
        url('#{$font-url}/AvenirLTStd-Black.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}

@font-face {
    font-family: 'AvenirLTStd';
    src: url('#{$font-url}/AvenirLTStd-Heavy.woff2') format('woff2'),
        url('#{$font-url}/AvenirLTStd-Heavy.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}


@font-face {
    font-family: 'AvenirLTStd';
    src: url('#{$font-url}/AvenirLTStd-Medium.woff2') format('woff2'),
        url('#{$font-url}/AvenirLTStd-Medium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'AvenirLTStd';
    src: url('#{$font-url}/AvenirLTStd-Roman.woff2') format('woff2'),
        url('#{$font-url}/AvenirLTStd-Roman.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

h1, h2, h3, h4, h5, h6, p, ul, ol, .h1, .h2, .h3, .h4, .h5, .h6 {
	margin-top: 0;
	margin-bottom: rem($globalMarginBottom);
}


h1, h2, h3, h4, h5, h6 {
    a {
        color: inherit;
        &:hover, &:focus {
            color: $primary;
            text-decoration: none;
        }
    }
}


// Font Weight

.font-weight-medium {
	font-weight: 500 !important;
}
.font-weight-semi {
	font-weight: 600 !important;
}



h1, h2, h3, h4, h5, h6 {
	&.h1, &.h2, &.h3, &.h4, &.h5, &.h6, .h1, .h2, .h3, .h4, .h5, .h6 {
		line-height: $headings-line-height;
	}
}

.h1, .h2, .h3, .h4, .h5, .h6 {
	line-height: $line-height-base;
}

strong {
	&.h1, &.h2, &.h3, &.h4, &.h5, &.h6 {
		font-weight: bold;
	}
}




// Heading

h1, .h1 {
	font-size: 20px;
}

h2, .h2 {
	font-size: 18px;
}

h3, .h3 {
	font-size: 16px;
}

h4, .h4 {
	font-size: 14px;
}

h5, .h5 {
	font-size: 12px;
}

h6, .h6 {
	font-size: 10px;
}



// Text Sizes


.text-sm {
	font-size: 11px;
}
.text-md {
	font-size: 13px;
}
.text-lg {
	font-size: 15px;
}
.text-xl {
	font-size: 17px;
}
.text-xxl {
	font-size: 19px;
}



// Text Colors


.text-gray {
	color: $gray;
}
.text-darker {
	color: $gray-darker;
}
.text-lighter {
	color: $gray-lighter;
}